.mobileNavbar {
    font-family: Lato;
}
.backdrop-container{
    display: none;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    -webkit-animation: fadeIn 1.2s ease-in-out;
    animation: fadeIn 1.2s ease-in-out;
}

nav {
    display: flex;
    flex-direction: row;
    z-index: 1;
    background-color: #282c34;
    padding: 18px 1.2em 18px 1.2em;
    width: 90%;
    position: fixed;
    top: 0;
}

.sideName {
    color: azure;
    font-size: 22px;
    position: absolute;
    width: 90%;
    text-align: center;
}

.mobile-nav-open-icon{
    font-size: 22px;
    cursor: pointer;
    margin-right: 2rem;
    color: whitesmoke;
    z-index: 2;
}

.sidenav-container {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidenav-container div {
    text-decoration: none;
    font-size: 1rem;
    color: #818181;
    display: block;
    transition: 0.3s;
    margin: 10px 0;
}

.sidenav-container div:hover {
    color: #f1f1f1;
}

.sidenav-container .closebtn {
    font-size: 3rem;
    font-weight: 700;
    color: #04AA6D;
    padding-right: 1rem;
}

.sidenav-container .drawer-close-button{
    height: 3rem;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 3rem;
}

div.sideTab {
    font-size: 20px;
}

