:root {
    --reactNative: #F1E05A;
    --react: #F1E05A;
    --javascript: #F1E05A;
    --swift: #FFAC45;
    --dart: #03B4AA;
    --java: #B07318;
}

.language-container {
    margin-bottom: 0.5em;
    padding: 0.2em 0.5em 0.2em 0.5em;
    border-radius: 5px;
    display: flex;
    font-family: Lato-Bold;
    justify-content: center;
    align-items: center;
    color: black;
    font-size: 1em;
}

.swift {
    background-color: var(--swift);
}

.react-native {
    background-color: var(--reactNative);
}

.react {
    background-color: var(--react);
}

.dart {
    background-color: var(--dart);
}

.java {
    background-color: var(--java);
}

.javascript {
    background-color: var(--javascript);
}
